/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */
/* -------=======GLOBAL========-------- */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */

@mixin Aero($size) {
  font-family: "Aeroport" !important;
  font-weight: $size !important;
}

*{
  @include Aero(300);
}

.wrapper-b{
  width: 1920px;
  margin: 0 auto;
}

.wrapper{
  width: 1440px;
  margin: 0 auto;
}


/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */
/* -------=======HEADER========-------- */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */

header{

  .wrapper-b{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 45px;

    .header_logo{
      padding: 28px 0;
    }

    .header_nav{
      display: flex;
      align-items: center;
      width: 100%;

      .nav_itemlist{
        padding: 30px;
        display: flex;
        align-items: center;

        .nav_item{

          a{
            font-size: 16px;
            line-height: 22px;
            transition: .3s;
            color: #B3BACC;
            padding: 30px 17px;
            @include Aero(400);
          }
          

          &:hover{

            a{
              color: #213243;
            }
            
          }
        }

        .nav_item-active{

          a{
            color: #213243;
          }
        }
      }

      .header_login{
        display: flex;
        align-items: center;
        margin-left: auto;

        input{
          font-size: 16px;
          line-height: 22px;
          color: #B3BACC;
          background: #EDEDED;
          border-radius: 10px;
          border: 0;
          padding: 13px 27px;
          width: 249px;
          margin-left: auto;
          margin-right: 49px; 

          &::placeholder{
            font-size: 16px;
            line-height: 22px;
            color: #B3BACC;
          }
        }
         
        .login_selection{ 
          display: flex;
          align-items: center;
          margin-right: 20px;

          .selection_text{
            font-size: 16px;
            line-height: 22px;
            color: #B3BACC;
            @include Aero(400);
          }

          input{
            display: none;
          }

          label{
            display: block;
            background: #E1ECFA;
            border-radius: 23px;
            width: 29px;
            height: 16px;
            position: relative;
            transition: .3s;
            margin: 20px;
            cursor: pointer;

            &::after{
              position: absolute;
              content: "";
              left: 2px;
              top: 50%;
              transform: translateY(-50%);
              width: 8px;
              height: 8px;
              background: white;
              border: 2px solid #1763F8;
              border-radius: 50%;
              transition: .3s;
            }
          }

          input:checked + label{

            &::after{
              left: calc(100% - 2px - 12px);
            }
          }

          .selection_text-active{
            color: #213243;
          }
        }

        .login_register,.login_log-in{
          @include Aero(400);
          font-size: 16px;
          line-height: 22px;
          color: #213243;
          cursor: pointer;
          transition: .4s;
        }

        .login_register{
          padding:32px 30px;

          &:hover{
            color: #1763F8;
            
          }
        }

        .login_log-in{
          background: #1763F8;
          color: white;
          padding:32px 50px;
        }
      }
    }
  }
}

